@import "@/assets/global.scss";





































































































.main-content {
  margin-top: 16px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  > span {
    margin-bottom: 12px;
  }
}

.association-btn {
  width: 100%;
  height: 46px;
  margin-bottom: 24px;
  padding: 0 24px;

  > div {
    box-sizing: border-box;
    border-radius: 6px;
    background: #FFFFFF;
    border: 1px solid #E9E9E8;
    padding: 11px 0;
  }
}
